@import './colors.less';

.reference-list {
    height: calc(100% - 10px);
    width: calc(100% - 20px);

    margin: 0 10px 10px;

    background: @grey-1;
    border-radius: 0 0 4px 4px;

    .reference-table {
        height: 100%;
        width: 100%;

        td, th, td .ant-btn {
            font-size: 16px;
        }
    }
}
@primary-color: #722ed1;